* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

#datetime-picker {
  width: 11rem;
  height: 2rem;
  text-align: center;
  color: #0c0c72;
  background-color: #c3cdce;
  border-radius: .5rem;
  font-size: 1.2rem;
  font-weight: bold;
}

button {
  height: 2rem;
  text-align: center;
  color: #0c0c72;
  background-color: #c3cdce;
  border-radius: .5rem;
  font-size: 1.2rem;
  font-weight: bold;
}

button:hover {
  cursor: pointer;
  background-color: #0ff;
}

button:disabled {
  cursor: not-allowed;
  background-color: coral;
}

.timer {
  gap: .75rem;
  display: flex;
}

.field {
  height: 3rem;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.value {
  margin-bottom: 0;
  padding-bottom: 0;
  font-size: 2.5rem;
}

.label {
  padding: 0;
  font-size: 1rem;
  font-weight: bold;
}

